import React from "react";
import "./hero.css";
import { useNavigate } from "react-router-dom";

function Hero() {
  const navigate = useNavigate();
  const scrollIntoView = (id) => {
    console.log('[Hero.js][scrollIntoView] id:', id)
    document.getElementById(id).scrollIntoView({
      behavior: "smooth",
    });
  };

  return (
    <div className="hero" data-aos="fade-up" id="hero">
      <div className="heroContent">
        <div className="heroInfo">
          <div>
            <h1>LegioPad</h1>
            <div className="btnGroup">
              <button className="claimBtn" onClick={() => navigate("/claim")}>
                Claim
              </button>
              <button
                className="stakeBtn"
                onClick={() => {
                  scrollIntoView("staking");
                }}
              >
                Stake
              </button>
            </div>
          </div>
        </div>
      </div>
      <div className="d-flex row g-0 about align-items-center ">
        <div className="col-md-9">
          <p className="h2">Legio Dao</p>
          <p className="h5">
            The smell of the sand, the roar echoing throughout the Colosseum,
            welcome to our Legio. We join as one to provide the best
            opportunities in crypto.
          </p>
        </div>
        <div className="col-md-3 my-2 text-center">
          <button>$LGO ON PCS</button>
        </div>
      </div>
    </div>
  );
}

export default Hero;
