import React from "react";
import "./prices.css";
import Legio from "../../assets/svgs/lgo.svg";
import Binance from "../../assets/crypto/binance.png";
import Bitcoin from "../../assets/crypto/bitcoin.png";
import Monero from "../../assets/crypto/monero.png";
import { useNavigate } from "react-router-dom";

const pricesData = [
  {
    icon: Legio,
    currencyLogo: Binance,
    socials: [],
    title: "Legio",
    totalRaise: "$25000",
    exchangeRate: "1 ORACULA = 0.17 USDT",
    subText: "Swap end  4/27/2022, 4:00:00 AM",
    subText2: "0 / 150 000 EYWA",
    progress: 10,
    curency_rate: 4701,
  },
  {
    icon: Legio,
    currencyLogo: Bitcoin,
    socials: [],
    title: "Gladiator",
    totalRaise: "$75000",
    exchangeRate: "1 TARO = 0.018 BUSD",
    subText: "Next claim at 4/27/2022, 7:30:00 AM",
    subText2: "994 194.444 / 1 666 666.666 TARO",
    progress: 20,
    curency_rate: 17895.5,
  },
  {
    icon: Legio,
    currencyLogo: Monero,
    socials: [],
    title: "Spectator",
    totalRaise: "$100000",
    exchangeRate: "1 EYWA = 0.2 BUSD",
    subText: "Whitelist start  TBA",
    subText2: "27 652.941 / 294 117.647 ORACULA",
    progress: 60,
    curency_rate: 0,
  },
];

function Prices() {
  const navigate = useNavigate();
  return (
    <div className="prices" id="prices" data-aos="fade-up">
      <div className="text-start col-md-12">
        <div className="heading-div">
          <span className="light-heading">IDO</span>
          <p className="subHeading text-white">IDO</p>
        </div>
        {/* <p className="text-grey">Participant IDO Stake</p> */}
      </div>
      <div className="row col-md-12">
        {pricesData &&
          pricesData.map((price, index) => {
            return (
              <div
                key={String(index)}
                className="pricesCard text-center col-md-3 col-sm-12 col-xs-12 my-3"
              >
                <img src={price.icon} className="mb-4" alt="" />
                <div
                  className="divider"
                  style={{ borderTop: "0.5px solid gray" }}
                />
                <div className="prices-row-cont">
                  <h3 className="">{price.title}</h3>
                  <img
                    src={price.currencyLogo}
                    className="currency-logo"
                    alt=""
                  />
                </div>
                <div className="prices-row-cont mt-2">
                  <h6 className="prices-label">Total Raise</h6>
                  <h6 className="prices-unit">{price.totalRaise}</h6>
                </div>
                <div className="prices-row-cont mt-2">
                  <h6 className="prices-label">Exchange Rate</h6>
                  <h6 className="prices-unit">{price.exchangeRate}</h6>
                </div>
                <div className="prices-row-cont mt-4">
                  <h6 className="prices-label">{price.subText}</h6>
                  <h6 className="prices-unit">{price.progress}%</h6>
                </div>
                <progress value={price.progress} max={100} />
                <div className="prices-row-cont">
                  <h6
                    className="prices-label"
                    style={{ justifyContent: "center" }}
                  >
                    <img
                      src={price.currencyLogo}
                      className="currency-logo"
                      alt=""
                    />{" "}
                    {price.curency_rate}
                  </h6>
                  <h6 className="prices-label">{price.subText2}</h6>
                </div>
                <button
                  onClick={() => {
                    navigate("/ido");
                  }}
                >
                  {price.subText}
                </button>
              </div>
            );
          })}
      </div>
    </div>
  );
}

export default Prices;