import React, { useEffect, useState } from "react";
import "./claim.css";

// 2022/05/02 https://www.geeksforgeeks.org/how-to-create-dialog-box-in-reactjs/ NOT support at React 17
// 2022/05/02 https://www.newline.co/@dmitryrogozhny/how-to-display-modal-dialog-in-react-with-react-modal--dbf46cda
import Modal from "react-modal";

import { ethers } from 'ethers';
import ButtonImg from "../../assets/icons/img/Button.png";
import Logo from "../../assets/icons/img/new.png";
import { smartContract } from '../../address-book.js'
import claimContractJson from '../../contracts/TokenClaim.json';

Modal.setAppElement("#root");

let claimContract
let claimAllocationP0
let claimAvailableP0
let claimTimeP0
let claimBtnP0
let consoleResult = ''

// Smart Contract
const onClaim = async () => {
  console.log('Do claim for phase 0, 1...')
  consoleResult = 'onClaim...' + '\n'
  const { ethereum } = window;
  consoleResult = 'ethereum...' + '\n'
  if (ethereum) {
    const provider = new ethers.providers.Web3Provider(ethereum)
    const signer = provider.getSigner(0)
    consoleResult += 'get signer' + '\n'
    const signAddress = await signer.getAddress() // would cause exception
    consoleResult += 'get address' + signAddress + '\n'
    consoleResult += '1' + '\n'

    let clm_phase0
    consoleResult += '2' + '\n'
    try{
      consoleResult += '... p0 ...' + '\n'
      console.log('test 1')
      clm_phase0 = await claimContract.connect(signer).claim(
        smartContract.bscToken,
        0,
        { from: signAddress }
      )
      console.log('test 2')
      await clm_phase0.wait();
      console.log('test 3')
      consoleResult += 'get p0 ok' + '\n'
    } catch (e) {
      console.log('test 4')
      console.log(e)
      //console.log('[error] claim0:' + e['data']['message'])
      //consoleResult += '[error] claim0:' + e['data']['message'] + '\n'
      clm_phase0 = ethers.utils.parseEther('0')
    }
    console.log('[onClaim] clm_phase0:' + ethers.utils.formatEther(clm_phase0))

    let clm_phase1
    try{
      consoleResult += '... p1 ...' + '\n'
      clm_phase1 = await claimContract.connect(signer).claim(
        smartContract.bscToken,
        1,
        { from: signAddress }
      )
      await clm_phase1.wait();
      consoleResult += 'get p1 ok' + '\n'
    } catch (e) {
      console.log('[error] claim1:', e['data']['message'])
      consoleResult += '[error] claim1:' + e['data']['message']
      clm_phase1 = ethers.utils.parseEther('0')
    }
    console.log('[onClaim] clm_phase1:' + ethers.utils.formatEther(clm_phase1))
  }
}

const initialClaim = async () => {
  console.log('Claim page start')
  consoleResult = ''
  try {
    const { ethereum } = window;
    if (ethereum) {
      const provider = new ethers.providers.Web3Provider(ethereum)
      const signer = provider.getSigner()
      claimContract = new ethers.Contract(smartContract.bscClaim, claimContractJson.abi, provider)

      let signAddress
      try {
        signAddress = await signer.getAddress()
      } catch (e) {
        consoleResult += 'cannot get address\n'
        claimAvailableP0.innerText = 'Not Available'
        claimBtnP0.onclick = null
        claimBtnP0.disabled = true
        console.log('Error, no account connected')
        return false
      }

      consoleResult += 'Get phase 0\n'
      let claimAmountPhase0 = await claimContract.getUserPhaseTokenClaim(
        smartContract.bscToken, 0, signAddress,)
        consoleResult += 'Get phase 1\n'
      let claimAmountPhase1 = await claimContract.getUserPhaseTokenClaim(
          smartContract.bscToken, 1, signAddress,)
      let claimAmount = claimAmountPhase0.add(claimAmountPhase1)
      let claimAmountString = ethers.utils.formatEther(claimAmount)
      console.log('Phase 0,1 can claim:', claimAmountString)
      claimAllocationP0.innerText = '\n' + claimAmountString

      consoleResult += '\n' + claimAmountString + '\n'

      var _claimTimeP0 = new Date(2022, 4-1, 12, 0, 0, 0); // month index 0 ~ 11
      claimTimeP0.innerText = _claimTimeP0.toLocaleString()
      // console.log('claimTimeP0:', claimTimeP0.innerText)
      if (Date.now() - _claimTimeP0.getTime() < 0){
        claimAvailableP0.innerText = 'Not Available'
        //claimBtnP0.onclick = null
        claimBtnP0.disabled = true
        consoleResult += 'claim not avaiable\n'
      } else {
        claimAvailableP0.innerText = 'Available'
        //claimBtnP0.onclick = onClaim
        claimBtnP0.disabled = false
        consoleResult += 'claim avaiable\n'
      }
    } else {
      consoleResult += 'Ethereum object not exist'
      console.log('Ethereum object not exist')
      console.log('Please install MetaMask!')
      claimAvailableP0.innerText = 'Not Available'
      claimBtnP0.onclick = null
      claimBtnP0.disabled = true
    }

  } catch (err) {
    consoleResult += '[err] ' + err
    console.log(err)
  }
}
function Claim() {

  //  componentDidMount  componentDidUpdate:
  useEffect(() => {
    //console.log('[claim] componentDidMount')
    claimAllocationP0 = document.getElementById('legioClaimAllocationP0')
    claimAvailableP0 = document.getElementById('legioAvailableP0')
    claimTimeP0 = document.getElementById('legioClaimTimeP0')
    claimBtnP0 = document.getElementById('claimBtnP0')
    //console.log('[useEffect] claimAllocationP0:', claimAllocationP0)
    initialClaim()

    // For console.log
    var old = console.log;
    var logger = document.getElementById('loglog');
    console.log('logger:', logger)
    console.log = function (message) {
        if (typeof message == 'object') {
            logger.innerHTML += (JSON && JSON.stringify ? JSON.stringify(message) : message) + '<br />';
        } else {
            logger.innerHTML += message + '<br />';
        }
    }
  });

  // For modal dialot
  const [isOpen, setIsOpen] = useState(false);

  function toggleModal() {
    setIsOpen(!isOpen);
  }
  
  
  
  return (
    <div className="claim"  id="claim">
      <div className="claim-bg">
        <div className="heading-div">
          <span className="light-heading">Claim</span>
          <p className="subHeading text-white">Claim</p>
        </div>
        <div className="claim-card">
          <img src={Logo} className="img-fluid" alt="" />
          <p>Your allocation<span id="legioClaimAllocationP0"></span></p>
          <div>
            <p>Claim start date</p>
            <p id="legioClaimTimeP0">2022-Mar-03 17:00 UTC</p>
          </div>
          <p id="legioAvailableP0">Not available</p>
          <div className="grey-bg">
            <button id="claimBtnP0" className="btn bg-zeus" type="button" disabled="" onClick={onClaim}>
              <img src={ButtonImg} alt="claim" />
            </button>
          </div>
        </div>

        <p>Version: 1.0.0.220502.10</p>

        <div>
          <button className="console-dialog" onClick={toggleModal}>
            Console log  
          </button>

          <Modal
            isOpen={isOpen}
            onRequestClose={toggleModal}
            contentLabel="My dialog"
            className="mymodal"
            overlayClassName="myoverlay"
            closeTimeoutMS={500}
          >
            <p> {consoleResult} </p>
            <button onClick={toggleModal}>Close modal</button>
          </Modal>
        </div>

        <p id="loglog"></p>
        


      </div>
    </div>
  );
}

export default Claim;
